
// Size
.btn-icon {
  font-size: 1rem;
  padding: 0;
  display: inline-block;
  text-align: center;
  border-radius: $border-radius;
  @include btn-icon-variant(35px);
}
.btn-icon-round {
  border-radius: 50%;
}
.btn-icon-sm {
  font-size: $font-size-base; // 14px
  @include btn-icon-variant(30px);
}
.btn-icon-md {
  @include btn-icon-variant(45px);
  font-size: 18px;
}
.btn-icon-lg {
  @include btn-icon-variant(65px);
  font-size: 28px;
}
.btn-icon-xl {
  @include btn-icon-variant(80px);
  font-size: 44px;
}



