

// Gradient text
.gradient-text {
  font-size: 150px;
  font-weight: 300;
  color: $primary; // fallback
  background: -webkit-linear-gradient(92deg, #fb83fa, #00aced);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.bold {
  font-weight: $font-weight-500;
}

h1,
h2 {
  .bold,
  &.bold {
    font-weight: $font-weight-light;
  }
}

.color-primary,
a.color-primary {  
  color: $primary;
  &:hover { color: $primary; }
}
.color-success,
a.color-success {  
  color: $success;
  &:hover { color: $success; }
}
.color-info,
a.color-info {  
  color: $info;
  &:hover { color: $info; }
}
.color-warning,
a.color-warning {  
  color: $warning;
  &:hover { color: $warning; }
}
.color-danger,
a.color-danger {  
  color: $danger;
  &:hover { color: $danger; }
}
.color-dark,
a.color-dark {  
  color: $dark;
  &:hover { color: $dark; }
}
.color-white,
a.color-white {  
  color: $white;
  &:hover { color: $white; }
}
.color-gray-darker,
a.color-gray-darker {  
  color: $gray-900;
  &:hover { color: $gray-900; }
}
.color-gray-dark,
a.color-gray-dark {  
  color: $gray-800;
  &:hover { color: $gray-800; }
}
.color-gray,
a.color-gray {  
  color: $gray-700;
  &:hover { color: $gray-700; }
}
.color-gray-light,
a.color-gray-light {  
  color: $gray-600;
  &:hover { color: $gray-600; }
}
.color-gray-lighter,
a.color-gray-lighter {  
  color: $gray-200;
  &:hover { color: $gray-200; }
}


// Dropcap
.dropcap,
.dropcap-square,
.dropcap-circle {
  display: block;
  float: left;
  font-weight: normal;
  margin-right: .25em;
  text-shadow: none;  
}

.dropcap {
  font-size: 2em;
}

.dropcap-square,
.dropcap-circle {
  background-color: $gray-200;
  color: $body-color;
  width: 36px;
  text-align: center;
}

.dropcap-square {
  border-radius: $border-radius;  
  font-size: 2.3em;
}

.dropcap-circle {
  border-radius: 50%;  
  font-size: 1.78em;
}

.dropcap.colored {
  color: $primary;
}

.dropcap-square.colored,
.dropcap-circle.colored {
  background-color: $primary;
  color: $white;
}


// Hightlight
.ui-highlight {
  background-color: $dark;
  color: $white;
  border-radius: $border-radius;  
  padding: 2px 5px;
  &.colored {
    background-color: $primary;
  }
}






